.credit-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.project-content .credit-person-wrapper {
  font-size: 0.7em;
  padding-right: 20px;
  margin-bottom: 5px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  max-width: 25%;
  min-width: 25%;
}

.project-content .credit-title {
  font-size: 0.6em;
  font-weight: 700;
}

a {
	color: black;
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}



@media screen and (max-width: 1100px) and (min-width: 740px) {
/*  .project-content .credit-person-wrapper {
