    min-width: 50%;
  }*/
}

/* Hamburger menu appears at this point */
@media screen and (max-width: 740px) {
  .project-content .credit-person-wrapper {
    max-width: 25%;
    min-width: 25%;
  }
}


/* Hamburger menu appears at this point */
@media screen and (max-width: 420px) {
  .project-content .credit-person-wrapper {
    max-width: 35%;
    min-width: 35%;
  }
}


